$mx-footer-vh: 30vh;
$mx-nav-min-height: 5rem;
$mx-layout-height-vh-10: 5rem;
@import "@shawnsandy/first-paint/index.scss";
@import "https://cdn.skypack.dev/@shawnsandy/blacktie/dist/bootstrap-lite.min.css";
body {
    main {
        min-height: 60vh;
    }
    nav[aria-label]:first-of-type {
        a {
            &:hover {
                background-color: transparent;
                color: gray;
                text-decoration: underline;
            }
        }
        section {
            padding: 0 2.5rem;
        }
    }
    header {
        background-color: rgba(0, 0, 0, 0.03);
        h2 {
            font-size: 5rem;
        }
        margin-bottom: 4rem;
    }
    footer {
        p {
            text-align: left;
        }
        li {
            a {
                font-size: 1rem;
                padding: 0 1rem;
                color: #fff;
            }
        }
    }
}
